<template>
  <div
    :style="{'height':computerHeight}"
    class="f16"
    style="background-color:#f6f6f6;position:fixed;top:50px;left:0px;right:0px;overflow-y:scroll;"
  >
    <div class="all t10 weight">
      <div>当前</div>
    </div>
    <div class="all">
      <div style="width:20%;text-align:left;">房间详情</div>
      <div style="width:60%;text-align:right;">{{ $route.query.scaleName }}-{{ $route.query.virtualRoomScale }}-{{ $route.query.roomSn }}</div>
    </div>
    <div class="all">
      <div>每期租金</div>
      <!-- *info.paymentMethod -->
      <div class="c_88">&yen;{{ roomRealAmount || info.price }}<span>&ensp;{{ $route.query.orderType == 3 ?d_paymentMethod:'/天' }}</span></div>
    </div>
    <div class="all">
      <div>到期日期</div>
      <div class="c_88">{{ queryEndTime || info.endTime }}</div>
    </div>
    <div class="all t10 weight">调整</div>
    <div
      class="all"
      @click="show_num = true"
    >
      <div>续签时长</div>
      <div class="pay">
        <span v-if="renew_time">{{ renew_time }}/月</span>
        <img src="../../assets/right.png" />
      </div>
    </div>
    <div
      class="all"
      v-if="!$route.query.cooperOrderId"
    >
      <div>到租日期</div>
      <div v-if="eTime">{{ eTime }}</div>
    </div>
    <div
      class="all"
      @click="show_pay = true"
      v-if="$route.query.orderType == 3"
    >
      <div>付款方式</div>
      <div class="pay">
        <span v-if="Pay_label">{{ Pay_label }}</span>
        <img
          src="../../assets/right.png"
          alt
        />
      </div>
    </div>
    <div class="all">
      <div>{{ $route.query.orderType == 3?'每期租金':'每日租金' }}</div>
      <input
        v-if="rentPrice_input"
        class="input_price"
        type="number"
        step="1"
        min="0"
        placeholder="请输入租金"
        v-model="rentPrice_input"
      />
      <span
        v-if="$route.query.orderType == 4"
        class="c_88"
      >/天</span>
    </div>
    <div
      id="bg"
      @click="bthRenew"
    >确定续租</div>
    <!-- 续签时长 -->
    <mt-popup
      v-model="show_num"
      position="bottom"
      style="width:100%;"
    >
      <mt-cell
        v-for="(item) in number"
        :key="item"
        :title="item"
        @click.native="chooseNum(item)"
      ></mt-cell>
    </mt-popup>
    <!-- 付款方式 -->
    <mt-popup
      v-model="show_pay"
      position="bottom"
      style="width:100%;"
    >
      <mt-cell
        v-for="(item,i) in compOptions"
        :key="i"
        :title="item.label"
        @click.native="choosePay(item)"
      ></mt-cell>
    </mt-popup>
  </div>
</template>
<script>
import '@/assets/styles/global.css';
import { Toast } from 'mint-ui';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessRenewalContractLastByOrderRoom, GetBusinessRenewalContractAdd } from '@/api/api_business_hotel_and_scale';
import { GetBusinessCooperRenewalContractLastByCooperOrderRoom, GetBusinessCooperRenewalContractAdd } from '@/api/api_business';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            btnStatus: true,
            info: {},
            roomPrice: null,
            options: [],
            Pay_label: null,
            number: ['1', '3', '6', '12', '24'],
            show_num: false,
            renew_time: null,
            eTime: null,
            show_pay: false,
            rentPrice: null,
            rentPrice_input: null,
            lastEndTime: null,
            roomRealAmount: null,
            queryEndTime: null,
            d_paymentMethod: null
        };
    },
    created() {
        this.$store.commit('setHeader', '续租');
        this.getPayment();
    },
    computed: {
        computerHeight() {
            return window.innerHeight + 'px';
        },
        compOptions() {
            return this.options.filter((item) => +item.value <= +this.renew_time);
        }
    },
    methods: {
        getPayment() {
            GetSystemDictDataByType({ dictType: 'business_payment_method' }).done((res) => {
                this.options = res.data.map((item) => {
                    return {
                        label: item.dictLabel,
                        value: item.dictValue
                    };
                });
                if (!this.$route.query.cooperOrderId) {
                    this.getLast();
                } else {
                    this.getCooperLast(this.$route.query.cooperOrderId);
                }
            });
        },
        // 根据订单房间查询最后一次合同
        getLast() {
            GetBusinessRenewalContractLastByOrderRoom(this.$route.query.orderRoomId)
                .then((res) => {
                    if (res && res.ret == 1) {
                        if (res.data) {
                            this.info = res.data;
                            this.renew_time = this.$route.query.orderType == '3' ? this.info.paymentMethod : '1';
                            this.rentPrice_input = res.data.price;
                            this.eTime = dayjs(dayjs(this.info.endTime).format('YYYY-MM-DD')).add(this.renew_time, 'month').format('YYYY-MM-DD');
                            this.d_paymentMethod = this.options.find((item) => item.value == this.renew_time).label;
                            this.Pay_label = this.options.find((item) => item.value == this.renew_time).label;
                            this.Pay_value = this.renew_time;
                            this.one_month = res.data.price / res.data.paymentMethod;
                        } else {
                            this.renew_time = this.$route.query.orderType == '3' ? this.$route.query.paymentMethod : '1';
                            this.roomRealAmount = this.$route.query.isprice;
                            this.rentPrice_input = this.$route.query.isprice;
                            this.queryEndTime = this.$route.query.endTime;
                            this.eTime = dayjs(dayjs(this.queryEndTime).format('YYYY-MM-DD')).add(this.renew_time, 'month').format('YYYY-MM-DD');
                            this.d_paymentMethod = this.options.find((item) => item.value == this.renew_time).label;
                            this.renew_time = this.renew_time;
                            this.Pay_label = this.options.find((item) => item.value == this.renew_time).label;
                            this.Pay_value = this.renew_time;
                            this.one_month = this.$route.query.isprice / this.$route.query.paymentMethod;
                        }
                    } else {
                        this.Commom.backLogin(res);
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 根据单位订单查询最后一次合同
        getCooperLast() {
            GetBusinessCooperRenewalContractLastByCooperOrderRoom({ cooperOrderRoomId: this.$route.query.cooperOrderRoomId }).done((res) => {
                if (res.data) {
                    this.info = res.data;
                    this.renew_time = this.$route.query.orderType == '3' ? this.info.paymentMethod : '1';
                    this.rentPrice_input = res.data.price;
                    this.eTime = dayjs(dayjs(this.info.endTime).format('YYYY-MM-DD')).add(this.renew_time, 'month').format('YYYY-MM-DD');
                    this.d_paymentMethod = this.options.find((item) => item.value == this.renew_time).label;
                    this.renew_time = this.renew_time;
                    this.Pay_label = this.options.find((item) => item.value == this.renew_time).label;
                    this.Pay_value = this.renew_time;
                    this.one_month = res.data.price / res.data.paymentMethod;
                } else {
                    this.renew_time = this.$route.query.orderType == '3' ? this.$route.query.paymentMethod : '1';
                    this.roomRealAmount = this.$route.query.roomRealAmount;
                    this.rentPrice_input = this.$route.query.roomRealAmount;
                    this.queryEndTime = this.$route.query.endTime;
                    this.eTime = dayjs(dayjs(this.queryEndTime).format('YYYY-MM-DD')).add(this.renew_time, 'month').format('YYYY-MM-DD');
                    this.d_paymentMethod = this.options.find((item) => item.value == this.renew_time).label;
                    this.Pay_label = this.options.find((item) => item.value == this.renew_time).label;
                    this.Pay_value = this.renew_time;
                    this.one_month = this.$route.query.roomRealAmount / this.$route.query.paymentMethod;
                }
            });
        },
        // 续签时长
        bthNum() {
            this.show_num = true;
        },
        // 续签时长-选择
        chooseNum(num) {
            this.renew_time = num;
            if (+this.Pay_value > +num) {
                this.Pay_value = num;
                this.Pay_label = this.options.find((item) => item.value == num).label;
            }
            if (this.info && this.info.endTime) {
                this.eTime = dayjs(this.info.endTime).add(this.renew_time, 'month').format('YYYY-MM-DD');
            } else {
                this.eTime = dayjs(this.queryEndTime).add(this.renew_time, 'month').format('YYYY-MM-DD');
            }
            this.show_num = false;
        },
        // 付款方式-选择
        choosePay(item) {
            this.Pay_label = item.label;
            this.Pay_value = item.value;
            this.rentPrice_input = Number(this.one_month * this.Pay_value).toFixed(2);
            this.show_pay = false;
        },
        // 确定续租
        bthRenew() {
            if (!this.$route.query.cooperOrderId) {
                if (this.rentPrice_input.length == '') {
                    Toast({
                        message: '请正确输入租金',
                        duration: 900
                    });
                    this.rentPrice_input = '';
                    return false;
                }
                let data = {
                    orderId: this.$route.query.orderId,
                    orderRoomId: this.$route.query.orderRoomId,
                    period: this.renew_time,
                    paymentMethod: this.$route.query.orderType == '3' ? this.Pay_value : '1',
                    price: this.rentPrice_input
                };
                if (!this.btnStatus) {
                    return;
                } else {
                    this.btnStatus = false;
                }
                GetBusinessRenewalContractAdd(data).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 900
                    });
                    setTimeout('window.history.go(-1)', 900);
                });
            } else {
                if (!this.btnStatus) {
                    return;
                } else {
                    this.btnStatus = false;
                }
                GetBusinessCooperRenewalContractAdd({
                    cooperOrderId: this.$route.query.cooperOrderId,
                    cooperOrderRoomId: this.$route.query.cooperOrderRoomId,
                    price: this.rentPrice_input,
                    period: this.renew_time,
                    paymentMethod: this.Pay_value
                })
                    .done((res) => {
                        Toast({
                            message: res.msg,
                            duration: 1000
                        });
                        setTimeout('window.history.go(-1)', 900);
                    })
                    .then((e) => {
                        this.btnStatus = true;
                    });
            }
        }
    }
};
</script>
<style scoped>
.weight {
    color: #000;
    font-weight: bold;
}
.pay > img {
    height: 18px;
    width: 18px;
    vertical-align: middle;
}
.input_price {
    border: none;
    text-align: right;
    color: #2e7bf7;
    outline: 0;
    text-align: right;
    width: 70%;
}
.c_88 {
    color: #888;
}
</style>



